
.message-detail {
  width: 100%;
  height: 80%;
  overflow-y: scroll;
  position: fixed;
  top: 20%;
  background: #dcdcdc;
}
.message-detail .back-btn {
  width: 100%;
  height: 32px;
  line-height: 32px;
  color: #666;
  font-size: 14px;
  padding-left: 10px;
  box-sizing: border-box;
  background: #fff;
  border-bottom: 1px solid #dcdcdc;
}
.message-detail .no-message {
  width: 100%;
  height: auto;
  text-align: center;
  color: #999;
  margin: 50px auto;
  font-size: 15px;
}
.message-detail .no-message:after {
  display: block;
  margin: 0px auto;
  font-family: "iconfont" !important;
  content: "\e659";
  font-size: 40px;
  color: #cccccc;
}
.message-detail .message-list {
  width: 100%;
  height: auto;
  background: #fff;
  border-bottom: 1px solid #dcdcdc;
  padding: 10px;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}
.message-detail .message-list span {
  -ms-flex-positive: 0;
      flex-grow: 0;
  font-size: 14px;
}
.message-detail .message-list span.reply-author {
  color: #00a2df;
}
.message-detail .message-list span.self-content {
  margin: 0 5px;
}
.message-detail .message-list span.reply-topic {
  color: #00a2df;
}



