
.load-pop[data-v-45ada9c6] {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 98;
}
.load-pop .load-box[data-v-45ada9c6] {
  width: 50%;
  height: auto;
  padding: 20px;
  margin: 300px auto;
  text-align: center;
  z-index: 99;
}
.load-pop .load-box .load-inner[data-v-45ada9c6] {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  border-radius: 50%;
  background-color: lightgreen;
  animation-name: load;
  animation-duration: 0.9s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: normal;
}
.load-pop .load-box .load-inner[data-v-45ada9c6]:first-child {
  animation-delay: .1s;
}
.load-pop .load-box .load-inner[data-v-45ada9c6]:nth-of-type(2) {
  animation-delay: .3s;
}
.load-pop .load-box .load-inner[data-v-45ada9c6]:last-child {
  animation-delay: .5s;
}
@keyframes load {
from {
    transform: scale(1);
}
50% {
    transform: scale(1.5);
}
to {
    transform: scale(1);
}
}



