
.user-profile {
  width: 100%;
  height: auto;
  border-bottom: 1px solid gainsboro;
}
.user-profile span.title {
  display: block;
  background: rgba(43, 43, 43, 0.09);
  padding: 2px 5px;
  box-sizing: border-box;
  color: #666;
  font-size: 10px;
}
.user-profile .user-info {
  width: 100%;
  height: auto;
  background: #fff;
  border-top: 1px solid gainsboro;
  color: #666;
  font-size: 12px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: 2px 5px;
  box-sizing: border-box;
}
.user-profile .user-info div.user-avar {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: gainsboro;
  overflow: hidden;
}
.user-profile .user-info div.user-avar img {
  width: 100%;
}
.user-profile .user-info div.user-name,
.user-profile .user-info div.user-score {
  margin: 0 10px;
  -ms-flex-preferred-size: auto;
      flex-basis: auto;
}
.user-profile .regis-time {
  width: 100%;
  padding: 2px 5px;
  box-sizing: border-box;
  color: #666;
  font-size: 12px;
}



