.router-view {
  position: absolute;
  top: 20%;
  height: 80%;
  background: #fff;
  width: 100%;
  overflow-x: hidden;
}
.fade-opac-enter-active,
.fade-opac-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-opac-enter,
.fade-opac-leave-active {
  opacity: 0;
  transform: scale(0.5);
}



